<template>
	<view class="body">
		<view class="content">
			<view class="cell">
				<view class="label">姓名：</view>
				<input type="text" placeholder="请输入姓名" v-model="name" @input="enterName"/> 
			</view>
			<view class="cell">
				<view class="label">报考院校：</view>
				<input type="text" v-model="school" placeholder="请选择院校" disabled @click="toSchool"/> 
			</view>
			<view class="cell">
				<view class="label">报考专业：</view>
				<input type="text" v-model="major" placeholder="请选择专业" disabled @click="toMajor"/> 
			</view>
		</view>
		<view class="submit" @click="toResult">提交</view>
	</view>
</template>

<script>
	import store from '@/store/index.js'; //引入store
	import { sch, updateSch } from '@/common/http.api.js';
	export default{
		data() {
			return {
				name:'',
				school:'',
				major:'',
				isUpdate:false, //是否是重新提交
			}
		},
		onLoad(option) {
			if(option.isUpdate){
				this.isUpdate = option.isUpdate
				console.log('this.isUpdate:',this.isUpdate)
			}
		},
		onShow() {
			this.token = store.state.vuex_token
			let that = this
			uni.getStorage({
				key: 'school',
				success: function (res) {
					console.log(res.data);
					that.school = res.data
				}
			});
			uni.getStorage({
				key: 'major',
				success: function (res) {
					console.log(res.data);
					that.major = res.data
				}
			});
			uni.getStorage({
				key: 'name',
				success: function (res) {
					console.log(res.data);
					that.name = res.data
				}
			});
		},
		methods: {
			toSchool() {
				uni.navigateTo({
					url:'/pages/volunteer/school'
				})
			},
			toMajor() {
				uni.navigateTo({
					url:'/pages/volunteer/major'
				})
			},
			toResult() {
				if(this.$u.utils.networkType()==true){
					let params = {
						name:'',
						area_id:'',
						school_id:'',
						major_id:'',
						token:this.token,
						id:''
					}
					params.id = uni.getStorageSync('id');
					params.name = uni.getStorageSync('name');
					params.area_id = uni.getStorageSync('area_id');
					params.school_id = uni.getStorageSync('school_id');
					params.major_id = uni.getStorageSync('major_id');
					if(this.isUpdate=='true'){
						updateSch({...params}).then(res=>{
							console.log(res)
							this.$u.toast(res.data.data)
							if(res.data.data!=-1){
								setTimeout(()=>{
									uni.navigateBack({
										delta:1
									})
								},1500)
							}
						}).catch(err=>{
							console.log(err)
						})
					}else{
						sch({...params}).then(res=>{
							console.log(res)
							this.$u.toast(res.data.data)
							if(res.data.data=='提交成功'){
								setTimeout(()=>{
									uni.navigateBack({
										delta:1
									})
								},1500)
							}
						}).catch(err=>{
							console.log(err)
						})
					}
				}else{
					this.$u.toast('当前网络状况不佳~')
				}
			},
			enterName() {
				uni.setStorage({
					key: 'name',
					data: this.name,
					success: function () {
						console.log('success');
					}
				});
			}
		}
	}
</script>

<style lang="scss">
	.body{
		width: 100vw;
		padding: 2vh 0;
		min-height: 96vh;
		background-color: #f7f6fb;
		.content{
			margin: 0 5%;
			border-radius: 20rpx;
			padding: 30rpx;
			background-color: #fff;
			.cell{
				display: flex;
				justify-content: space-between;
				align-items: center;
				font-size: 28rpx;
				margin-bottom: 40rpx;
				input{
					width: 65%;
					border: 1rpx solid #eee;
					height: 68rpx;
					padding: 0 20rpx;
					line-height: 68rpx;
					background-image: url(../../static/volunteer/right_icon.png);
					background-repeat: no-repeat;
					background-position: 96% center;
					background-size: 10rpx 20rpx;
				}
			}
		}
		.submit{
			text-align: center;
			position: fixed;
			bottom: 10vh;
			left: 5%;
			right: 5%;
			color: #fff;
			background: linear-gradient(to bottom, #dadada,#aaa);
			line-height: 78rpx;
			font-size: 30rpx;
			border-radius: 50rpx;
		}
	}
</style>